.mainContainer {
    width: 100%;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    background-color: white;
  }
  
  .contentContainer {
    width: 100%;
  }
  
  .mainBoxContainer {
    width: 100%;
    height: auto;
  
    display: flex;
    flex-direction: column;
  
    align-items: center;
  }
  
  .mainBox {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .findCharacterContainer{
    margin-top: 50px;
    margin-bottom: 50px;
    border: 0.2px solid rgb(223, 223, 223);
    border-radius: 5px;
    padding: 15px;
    width: 150px;
    height: 230px;
  }
  .findCharacterTitle{
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 0.2px solid rgb(223, 223, 223);
    padding-bottom: 15px;
  }
  
  .findCharacterImg{
    display: flex;
    justify-content: center;
  }

  .inputContainer{
    margin-top: 50px;
    width: 500px;
  }
  .inputFormBox{
    display: flex;
    justify-content: center;
    position: relative;
  }
  .inputField{
    width: 250px;
    padding: 5px;
    border-radius: 5px;
  }
  
  .inputTitle{
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
  }

  .searchIcon{
    position: absolute;
    width: 20px;
    right: 125px;
    bottom: 5px;
    cursor: pointer;
  }