.mainBoxContainer {
  width: 100%;

  display: flex;
  flex-direction: column;

  align-items: center;

  
  /* justify-content: center; */
}

.mainBox {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.statContainer {
  margin-top: 50px;
  margin-bottom: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;

  align-items: center;
}

.statBox {
  width: 900px;
  display: flex;
  flex-direction: row;
}

.hyperBox {
  font-size: 0.9rem;
}

.abilityBox {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  padding: 5px;
  width: 250px;
  height: 200px;
}

.abilityTitle {
  padding: 10px;
  font-size: large;
}

.abilityBox > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.abilityEach {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 8px;
  width: 230px;
  padding: 3px;
  padding-left: 2px;
  padding-right: 2px;
  text-align: center;
  margin-bottom: 3px;
}

.abilityEachBox {
  height: 130px;
}

.hyperAndTitleBox {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  width: 250px;
  padding: 10px;
  height: 390px;
}

.hyperEach {
  margin-bottom: 3px;
}

.mainStatBox {
  /* border: 0.5px solid rgb(223, 223, 223); */
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  width: 570px;
}
.mainStatBoxInner {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  width: 100%;
}
.mainStatBoxInner > div {
  width: 45%;
  padding: 5px;
}
.mainStatBoxInner > div:first-child {
  margin-right: auto;
}

.eachStatBox {
  display: flex;
  flex-direction: row;
  margin-bottom: 5px;
}
.eachStatBox > div:first-child {
  margin-right: auto;
}

.detailBox {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  width: 100%;
}
.detailBox > div {
  width: 45%;
  padding: 5px;
}
.detailBox > div:first-child {
  margin-right: auto;
}

.detailBox2 {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  width: 100%;
}
.detailBox2 > div {
  width: 45%;
  padding: 5px;
}
.detailBox2 > div:first-child {
  margin-right: auto;
}

.legendary {
  background-color: #d6ffd5; /* 초록색 */
}
.unique {
  background-color: #feffb2; /* 노란색 */
}
.epic {
  background-color: #f9b8ff; /* 보라색 */
}

.titlebox {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: xx-large;
}

.buttonBox {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.buttons > button {
  margin-right: 5px;
  border: 0.5px solid rgb(223, 223, 223);
}
.buttons > button:last-child {
  margin-right: 0px;
}

@media only screen and (max-width: 600px) {
  .mainBox {
    display: flex;
    flex-direction: column;
    width: 95%;
  }

  .statBox {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    justify-content: center;
  }
  .statBox >div{
    margin-bottom: 15px;
  }
  .titlebox > div{
    font-size: 1.7rem;
  }
  .detailBox{
    font-size: small;
  }
}
