.mainContainer {
  flex-grow: 1;
  width: 100%;

  display: flex;
  flex-direction: column;

  align-items: center;

  background-image: url("./img/루시드빅.jpg");
  background-size: cover;
  background-position: center;
  background-color: rgba(255, 255, 255, 0.2); /*투명도 조절 */
}

.contentsBox {
  width: 100%;
  /* height: 500px; */
  height: 100%;

  display: flex;

  justify-content: center;
  align-items: center;
}
.searchBar {
  position: relative;
  width: 40%;
}
.searchIcon {
  position: absolute;
  width: 30px;
  left: 100%;
  bottom: 10px;
  cursor: pointer;
}
.searchBar > form {
  width: 100%;
  height: 100%;
}
.inputField {
  width: 100%;
  height: 45px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 10px;
}

@media only screen and (max-width: 600px) {
  .searchBar {
    position: relative;
    width: 90%;
  }
  .searchIcon {
    position: absolute;
    width: 30px;
    left: 90%;
    bottom: 10px;
    cursor: pointer;
  }
  .searchBar > form {
    width: 100%;
    height: 100%;
  }
  .inputField {
    width: 90%;
    height: 45px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 10px;
  }
}
