.mainBoxContainer {
  width: 100%;

  display: flex;
  flex-direction: column;

  align-items: center;

  /* border: 1px solid black; */
}

.mainBox {
  width: 60%;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;

  /* border: 1px solid blue; */
}

.UnionBox {
  width: 200px;
  height: 300px;
  /* border: 1px solid yellow; */
}

.rankingBox {
  margin-top: 50px;
  width: 900px;

  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.rankingBox > div {
  width: 180px;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(223, 223, 223);
  border-radius: 5px;
  margin-right: 10px;
  align-items: center;
  padding: 5px;
  height: 280px;
}
.rankingBox > div:last-child {
  margin-right: 0px;
}

.eachImgbox {
  width: 175px;
  height: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}
.eachImgbox > img {
  height: 100%;
}

.line {
  width: 100%;
  border-bottom: 1px solid rgb(223, 223, 223);
  margin-bottom: 15px;
}

/* .rankingBox > div:last-child {
  margin-right: 0;
} */

.unionTetrisContainer {
  width: 100%;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.unionTetrisTitle {
  font-size: xx-large;
  padding: 15px;
}
.unionTetrisBox {
  display: flex;
  flex-direction: row;
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  width: 900px;
  height: 500px;
  position: relative;
}

.firstText {
  position: absolute;
  top: 140px;
  left: 30px;
}

.secondText {
  position: absolute;
  top: 340px;
  left: 30px;
}
.thirdText {
  position: absolute;
  top: 140px;
  left: 380px;
}
.fourthText {
  position: absolute;
  top: 340px;
  left: 390px;
}
.fifthText {
  position: absolute;
  top: 55px;
  left: 130px;
}
.sixthText {
  position: absolute;
  top: 55px;
  left: 310px;
}

.seventhText {
  position: absolute;
  top: 410px;
  left: 130px;
}
.eighthText {
  position: absolute;
  top: 410px;
  left: 310px;
}

.unionTetrisImgBox {
  width: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.unionTetrisTextBox {
  width: 400px;

  display: flex;
  flex-direction: column;
  padding-top: 15px;
  padding-bottom: 15px;
}

.unionOccupiedStat {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  width: 380px;
  height: 220px;
  overflow: auto;
  margin-bottom: auto;
  padding: 5px;
}

.unionRaiderStat {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  width: 380px;
  height: 220px;
  padding: 5px;
  overflow: auto;
}

.unionArtifactContainer {
  margin-top: 50px;

  display: flex;
  flex-direction: column;

  align-items: center;

  width: 100%;
  height: auto;
}

.unionArtifactTitle {
  font-size: xx-large;
  padding: 15px;
}

.unionArtifactbox {
  padding: 5px;
  display: flex;
  flex-direction: row;
  width: 900px;
  height: auto;
}

.unionArtifactImgBox {
  width: 620px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.unionArtifactTextBox {
  display: flex;
  flex-direction: column;
  width: 260px;
  height: auto;
}

.unionArtifactEachImgBox {
  width: 200px;
  height: 230px;
  border: solid rgb(223, 223, 223) 1px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;

  align-items: center;
  margin-bottom: 2px;
  margin-right: 2px;
}

.unionArtifactCrystalLevel {
  width: 190px;
  height: 29px;
  display: flex;
  justify-content: center;
  align-items: center;

  border-bottom: 1px solid rgb(223, 223, 223);
}

.unionArtifactCrystalImg {
  width: 190px;
  height: 199px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.unionArtifactCrystalImg > div {
  cursor: pointer;
}

.unionArtifactDetailBox {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  margin-bottom: auto;
  height: 330px;
  padding: 5px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.unionArtifactEffectBox {
  border: 0.5px solid rgb(223, 223, 223);
  border-radius: 5px;
  height: 330px;
  padding: 5px;
  width: 100%;
  overflow: auto;
}

.unionArtifactEffectBoxInner {
  display: flex;
  flex-direction: column;
}

.unionArtifactEffectTitle {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 5px;

  border-bottom: 0.5px solid rgb(223, 223, 223);
  margin-bottom: 10px;
}

.unionArtifactOptions > div {
  margin-bottom: 5px;
}

.unionArtifactOptions2 {
  margin-bottom: 5px;
}

