.headerContainer {
  display: flex;
  flex-direction: column;

  width: 100%;

  align-items: center;

  height: 120px;
}
.firstBox {
  display: flex;
  flex-direction: row;
  width: 60%;
  /* align-items: center; */

  height: 70px;
  justify-content: center;

  border-bottom: 0.5px solid rgb(223, 223, 223);
}
.logoBox {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background-image: url("./img/chory.gg.png");
  background-size: cover;
  background-position: center; */
}
.logoBox > div {
  font-size: x-large;
  font-weight: bolder;
  cursor: pointer;
}

.searchBox {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  position: relative;
}
.searchBox > form {
  width: 70%;
}

.searchBox input {
  width: 100%;
  height: 30px;

  padding-left: 10px;
  padding-right: 10px;

  border-radius: 7px;
}

.searchIcon {
  position: absolute;
  width: 25px;
  right: 12.5%;
  bottom: 22px;
  cursor: pointer;
}

.discord {
  width: 20%;
}
.discord img {
  margin-top: 5px;
  margin-left: 80px;
}
.secondBox {
  display: flex;
  flex-direction: row;
  height: 50px;
  width: 60%;

  align-items: center;
  border-bottom: 0.5px solid rgb(223, 223, 223);
}

.secondBox div {
  width: 100px;

  text-align: center;
}

@media only screen and (max-width: 600px) {
  .headerContainer {
    display: flex;
    flex-direction: column;

    width: 100%;

    align-items: center;

    height: 120px;
  }

  .firstBox {
    display: flex;
    flex-direction: row;
    width: 100%;
    /* align-items: center; */

    height: 70px;
    justify-content: center;

    border-bottom: 0.5px solid rgb(223, 223, 223);
  }

  .logoBox {
    width: 24%;
    /* background-image: url("./img/chory.gg.png");
    background-size: cover;
    background-position: center; */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .logoBox > div {
    font-size: large;
    font-weight: bolder;
    cursor: pointer;
  }

  .searchBox {
    width: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: relative;
  }
  .searchBox > form {
    width: 80%;
  }

  .searchBox input {
    width: 100%;
    height: 30px;

    padding-left: 10px;
    padding-right: 10px;

    border-radius: 7px;

    font-size: 0.7rem;
  }

  .searchIcon {
    position: absolute;
    width: 25px;
    right: 5%;
    bottom: 22px;
    cursor: pointer;
  }

  .discord {
    display: none;
  }

  .secondBox {
    display: flex;
    flex-direction: row;
    height: 50px;
    width: 100%;

    align-items: center;
    justify-content: center;
    border-bottom: 0.5px solid rgb(223, 223, 223);
  }
}
