.footerContainer {
  width: 100%;
  height: 200px;

  display: flex;
  flex-direction: column;

  align-items: center;
}

.sightName {
  border-top: 0.5px solid rgb(223, 223, 223);
  border-bottom: 0.5px solid rgb(223, 223, 223);
  position: relative;
}
.sightName img {
  position: absolute;
  right: 5%;
  bottom: 0%;
}

.footerContents {
  width: 60%;
}

@media only screen and (max-width: 600px) {
  .footerContents {
    width: 90%;
  }
}
