.headerContainer {
  border-bottom: 0.5px solid rgb(223, 223, 223);

  width: 60%;

  display: flex;
  flex-direction: row;

  height: 40px;

  align-items: center;
}

.headerContainer div {
  text-align: center;
  width: 120px;
  cursor: pointer;
}

.activeTab {
  font-weight: bold;
  font-size: larger;
}

@media only screen and (max-width: 600px) {
  .headerContainer {
    border-bottom: 0.5px solid rgb(223, 223, 223);

    width: 100%;

    display: flex;
    flex-direction: row;

    height: 40px;

    align-items: center;

    font-size: 0.8rem;
  }
}
