@media only screen and (min-width: 601px) {
  .mainBoxContainer {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;

    align-items: center;
  }

  .mainBox {
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .line {
    border-top: 0.2px solid rgb(223, 223, 223);
    margin-top: 3px;
    padding-bottom: 3px;
  }

  .mainStat {
    font-size: larger;
  }

  .equipPresetButtonBox {
    position: absolute;
    top: 20px;
  }
  .equipPresetButtonBox > Button {
    background-color: white;
    border: 0.2px solid rgb(223, 223, 223);
    border-radius: 5px;
    cursor: pointer;
  }

  .equipmentCashEquipButtonBox {
    position: absolute;
    top: 0px;
    left: 210px;
  }

  .equipmentCashEquipButtonBox > Button {
    background-color: white;
    border: 0.2px solid rgb(223, 223, 223);
    border-radius: 5px;
    cursor: pointer;
  }

  .equipCashPresetButtonBox {
    position: absolute;
    top: 20px;
    left: 140px;
  }
  .equipCashPresetButtonBox > button {
    background-color: white;
    border: 0.2px solid rgb(223, 223, 223);
    border-radius: 5px;
    cursor: pointer;
  }

  .equipmentAndStatBox {
    padding-top: 40px;
    width: 1200px;
    height: auto;
    display: flex;
    flex-direction: row;

    margin-top: 50px;
    justify-content: center;

    position: relative;
  }

  .statBox {
    overflow: auto; /* 내용이 넘칠 경우 스크롤이 생기도록 설정 */
  }

  .statBox > div {
    margin-bottom: 1px;
  }

  .equipImgBox {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 220px;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    height: 350px;
    margin-right: 5px;
    padding: 7px;
  }

  .equipImgBox > div {
    width: 41px;
    height: 41px;
    border: 0.3px solid rgb(223, 223, 223);
    border-radius: 5px;
    margin-right: 1px;
    margin-bottom: 1px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .equipImgBox > div > div {
    width: 99%;
    height: 99%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;

    cursor: pointer;
  }

  /* 특별한 스타일을 적용할 div들에 대한 선택자 */
  .equipImgBox > div:nth-child(2),
  .equipImgBox > div:nth-child(4),
  .equipImgBox > div:nth-child(9),
  .equipImgBox > div:nth-child(27) {
    border: 1px solid transparent; /* 투명border */
  }
  .itemBox > div {
    width: 100%;
  }

  .legendaryItem {
    border: 0.3px solid #60ff5b;
    background-color: #d6ffd5;
  }

  .uniqueItem {
    border: 0.3px solid #ffee00;
    background-color: #feffb2;
  }

  .epicItem {
    border: 0.3px solid #f04eff;
    background-color: #f9b8ff;
  }

  .equipMenuBox {
    overflow: auto; /* 내용이 넘칠 경우 스크롤이 생기도록 설정 */
    width: 220px;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    height: 350px;
    margin-right: 5px;
    padding: 7px;
    resize: vertical;
  }

  .equipMenuItemBoxBox {
    display: flex;
    flex-direction: column;
  }

  .equipMenuItemBox {
    cursor: pointer;

    display: flex;
    flex-direction: row;

    border: 0.5px solid rgb(223, 223, 223);

    border-radius: 5px;

    width: 93%;
    height: 35px;

    font-size: smaller;
    padding: 5px;
    margin-bottom: 10px;
  }

  .equipMenutextBox {
    display: flex;
    flex-direction: column;
  }
  .equipMenutextBox > div {
    display: flex;
    flex-direction: row;
  }

  .equipMenuImgBox {
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 12px;
    margin-left: 3px;
  }

  .firstText > div {
    margin-right: 5px;
    margin-bottom: 5px;
  }

  .options {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .options > div {
    margin-bottom: 1px;
    font-size: 0.8rem;
  }

  .itemTitleBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }
  .itemTitleBox > div {
    text-align: center;
  }

  .equipInfoBox {
    width: 220px;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    height: 350px;
    margin-right: 5px;
    padding: 7px;
    overflow: auto; /* 내용이 넘칠 경우 스크롤이 생기도록 설정 */
    resize: vertical;
  }

  .itemImageBox {
    margin-top: 15px;
    padding-bottom: 15px;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .itemImageBox > img {
    width: 40px;
  }

  .equipTextBoxStarImg {
    display: flex;
    flex-direction: row;

    justify-content: center;
    align-items: center;

    border: 0.1px solid rgb(223, 223, 223);
    border-radius: 2px;
    width: 40px;
    padding: 1px;
    font-size: 0.72rem;
  }

  .cashMenuBox {
    overflow: auto; /* 내용이 넘칠 경우 스크롤이 생기도록 설정 */
    width: 220px;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    height: 350px;
    margin-right: 5px;
    padding: 7px;
    resize: vertical;
  }

  .legendaryBorder {
    border: 1px solid #96ff92; /* 초록색 */
  }

  .uniqueBorder {
    border: 0.1px solid #fcff34; /* 노란색 */
  }

  .epicBorder {
    border: 0.1px solid #f9b8ff; /* 보라색 */
  }

  .legendary {
    color: rgb(50, 255, 50); /* 초록색 */
  }
  .unique {
    color: rgb(255, 255, 0); /* 노란색 */
  }
  .epic {
    color: rgb(189, 0, 189); /* 보라색 */
  }

  .arcaneSymbolBox {
    display: flex;
    flex-direction: row;
  }

  .arcaneSymbolBox2 {
    display: flex;
    flex-direction: column;
  }

  .arcaneSymbols {
    display: flex;
    flex-direction: row;
  }

  .symbolBox {
    width: 900px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 80px;
    align-items: center;
    justify-content: center;
  }

  .symbolImgBox {
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    width: 400px;
    padding: 20px;
    height: 250px;
  }

  .symbolTextBox {
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    width: 400px;
    /* min-width: 450px; */
    padding: 20px;
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .symbolTextBox > div {
    /* border: #60ff5b 1px solid; */
  }

  .symbolTitle {
    text-align: center;
    font-size: larger;
    margin-bottom: 20px;
  }

  .symbolItem {
    display: flex;
    flex-direction: column;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 10px;
    padding: 6px;
    margin-right: 10px;
    width: 50px;

    cursor: pointer;
  }

  .symbolItem:last-child {
    margin-right: 0;
  }

  .petContainer {
    width: 500px;
    margin-top: 80px;
    height: auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }

  .petContainerTitle {
    text-align: center;
    font-size: larger;
    margin-bottom: 20px;
  }

  .petContainerBox {
    display: flex;
    flex-direction: row;
    width: 400px;
  }

  .petBox {
    display: flex;
    flex-direction: column;
    width: 120px;
    height: 100px;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
  }

  .petBox:last-child {
    margin-right: 0px;
  }

  .petImgName {
    display: flex;
    flex-direction: row;
  }

  .petSkills {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .petSkills > div {
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    padding: 4px;
  }
  .petSkills > div:first-child {
    margin-right: 5px;
  }
  .characterSetInfoContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }

  .characterSetInfoBox {
    margin-top: 20px;

    display: flex;
    flex-direction: column;
    width: 900px;

    align-items: center;
    justify-content: center;
  }

  .flexBox {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .characterSetInfoTitleBox {
    font-size: larger;
    margin-bottom: 10px;
  }

  .setItemBox {
    width: 28%;
    display: flex;
    flex-direction: row;
    border: 0.5px rgb(223, 223, 223) solid;
    border-radius: 8px;
    margin-right: 30px;
    margin-bottom: 15px;
    padding-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .setItemImgBox {
    margin-right: 7px;
  }

  .setItemTextBox {
    display: flex;
    align-items: center;
  }
}

