@media only screen and (min-width: 601px) {
  .mainBoxContainer {
    width: 100%;
  
    display: flex;
    flex-direction: column;
  
    align-items: center;
  }
  
  .mainBox {
    display: flex;
    width: 60%;
    /* justify-content: center; */
    /* align-items: center; */
    flex-direction: column;
    align-items: center;
  }
  
  .skillContainer {
    /* width: 90%; */
    display: flex;
    flex-direction: row;
    margin-top: 15px;
  }
  
  .skillsBox {
    border: 1px rgb(223, 223, 223) solid;
    width: 480px;
    height: 200px;
    padding: 10px;
    border-radius: 8px;
  
    margin-right: auto;
  
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  
  .skillTextBox {
    border: 1px rgb(223, 223, 223) solid;
    width: 480px;
    height: 200px;
    padding: 10px;
    border-radius: 8px;
  
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
  }
  
  .skillImgAndNameBox {
    border: 1px solid rgb(223, 223, 223);
    border-radius: 5px;
    padding: 5px;
    margin-right: 5px;
    width: 35px;
    height: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  .skillImgAndNameBox:nth-child(even) {
    /*짝수만*/
    margin-top: 30px;
  }
  
  .skillLevelBox {
    margin-top: -5px;
    border: 0.5px solid black;
    width: 20px;
    height: 10px;
    background-color: black;
    border-radius: 4px;
    color: white;
    font-size: 0.65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .skillText > div {
    margin-top: 5px;
    padding-bottom: 5px;
    border-bottom: 0.5px solid rgb(223, 223, 223);
  }
  
  .skillText > div:last-child {
    border-bottom: none;
  }
  
  .pieceAndSolContainer {
    width: 1000px;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    padding: 5px;
  }
  
  .pieceAndSolBoxBox {
    display: flex;
    flex-direction: row;
  }
  
  .pieceAndSolBox {
    display: flex;
    flex-direction: row;
    border: 0.5px solid rgb(223, 223, 223);
    padding: 5px;
    border-radius: 5px;
  }
  
  .hexaTitle {
    display: flex;
    justify-content: center;
    font-size: 1.3rem;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  
  .pieceAndSolBox > div:first-child {
    margin-right: 5px;
  }
  .skillDetail {
    display: flex;
    flex-direction: row;
  }
  .skillDetailPiece {
    display: flex;
    flex-direction: row;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    padding: 2px;
  }
  
  .solImgBox {
    margin-top: 2px;
    margin-bottom: -2px;
  }
  
  .hyperContainer {
    margin-top: 40px;
    width: 1000px;
    display: flex;
    flex-direction: column;
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
  }
  
  .hyperBox {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    padding: 3px;
  }
  
  .hyperEach {
    border: 0.5px solid rgb(223, 223, 223);
    border-radius: 5px;
    padding: 5px;
    margin-right: 5px;
    margin-bottom: 5px;
  }
  
  .hyperTitle {
    display: flex;
    justify-content: center;
    margin-top: 15px;
    margin-bottom: 15px;
    font-size: 1.3rem;
  }
}

