.mainBoxContainer {
  width: 100%;

  display: flex;
  flex-direction: column;

  align-items: center;
}

.mainBox {
  width: 60%;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
}

.characterInfoBox {
  margin-top: 25px;
  border: 1.5px solid rgb(177, 177, 177);
  border-radius: 15px;
  height: 300px;
  width: 970px;
  display: flex;
  flex-direction: row;

  background-image: url("./img/크리스마스투명2.png");
  background-size: cover;
  background-position: center;
}
.characterImageBox {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.characterImageBox img {
  width: 80%;
}

.characterSummaryBox {
  width: 35%;
}
.characterName {
  font-size: x-large;
}
.textBox {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
}

.chracterMainStatText {
  display: flex;
  flex-direction: column;
  font-size: xx-large;
  border-top: 0.5px solid rgb(223, 223, 223);
  margin-top: 8px;
  padding-top: 8px;
}

.textBox div {
  margin-bottom: 7px;
}
.flexBox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.characterSetInfoTitleBox {
  font-size: larger;
  margin-bottom: 10px;
}

.setItemBox {
  width: 28%;
  display: flex;
  flex-direction: row;
  border: 0.5px rgb(223, 223, 223) solid;
  border-radius: 8px;
  margin-right: 30px;
  margin-bottom: 15px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.setItemImgBox {
  margin-right: 7px;
}

.setItemTextBox {
  display: flex;
  align-items: center;
}

.characterSymbolBox {
  margin-left: 40px;
  width: 28%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.symbolEquipmentContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.symbolItem {
  width: 12%;
  display: flex;
  flex-direction: column;
  margin-right: 10px;
  margin-bottom: 5px;
  border: 0.3px solid rgb(223, 223, 223);
  border-radius: 15px;
  font-size: x-small;
}
.arcaneForce {
  margin-top: 20px;
  margin-bottom: 5px;
}

.serverImageBox {
  display: flex;
  flex-direction: row;
  font-size: large;
}

@media only screen and (max-width: 600px) {
  .mainBoxContainer {
    width: 100%;

    display: flex;
    flex-direction: column;

    align-items: center;
  }

  .mainBox {
    width: 100%;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
  }

  .characterInfoBox {
    margin-top: 25px;
    border: 1.5px solid rgb(177, 177, 177);
    border-radius: 15px;
    height: 250px;
    width: 99%;
    display: flex;
    flex-direction: row;

    background-image: url("./img/크리스마스투명2.png");
    background-size: cover;
    background-position: center;
  }

  .characterSymbolBox {
    display: none;
  }

  .characterImageBox {
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .characterSummaryBox {
    width: 55%;
    font-size: 0.75rem;
  }
  .chracterMainStatText {
    margin-top: 0px;
    font-size: 1.15rem;
  }

  .characterImageBox img {
    width: 95%;
  }
  .characterDetail{
    font-size: 0.95rem;
  
  }
  .textBox{
    margin-top: 5px;
  }
}
